#port {
  width: 100%;
  margin-top: 30vh;
  // overflow: hidden;
}
.port__inner {
  padding: 16px;

  .port__title {
    width: 100%;
    height: 5vw;
    font-size: 4vw;
    font-weight: 600;
    line-height: 1.7;
    // font-family: var(--mainKor-font);
    text-transform: uppercase;
    color: var(--black100);
    border-bottom: 0.4vw solid var(--black100);
    margin-bottom: 16px;
    text-indent: -0.38vw;

    em {
      font-size: 1.25rem;
      font-weight: 400;
      line-height: 2;
    }
  }
  .port__wrap {
    display: flex;
    flex-wrap: wrap;
    width: 7000px;

    .port__item {
      width: 500px;
      height: 70vh;
      // background-color: var(--subBg100);
      padding: 2.5rem;
      margin-right: 20px;

      &.p1 {
        // background-color: var(--subBg100);
        border: 2px solid var(--black100);
      }
      &.p2 {
        // background-color: var(--subBg200);
        border: 2px solid var(--black100);
      }
      &.p3 {
        // background-color: var(--subBg300);
        border: 2px solid var(--black100);
      }
      &.p4 {
        // background-color: var(--subBg400);
        border: 2px solid var(--black100);
      }
      &.p5 {
        // background-color: var(--subBg500);
        border: 2px solid var(--black100);
      }
      &.p6 {
        // background-color: var(--subBg100);
        border: 2px solid var(--black100);
      }
      &.p7 {
        // background-color: var(--subBg200);
        border: 2px solid var(--black100);
      }
      &.p8 {
        background-color: var(--subBg300);
      }
      &.p9 {
        background-color: var(--subBg400);
      }
      &.p10 {
        background-color: var(--subBg500);
      }
      .num {
        font-size: 2rem;
        font-family: var(--mainNum-font);
      }
      .img {
        display: block;
        margin-top: -10px;
        transition: margin-top 0.3s;

        border-radius: 5px;
        // filter: saturate(0%);
        transition: all 0.3s;

        &:hover {
          margin-top: 0;
          // filter: saturate(100%);
        }
      }
      .title {
        font-size: 1.5rem;
        text-align: center;
        padding: 0.8rem 0;
        font-weight: 700;
        color: var(--black100);
        border-bottom: 2px solid var(--black100);
        margin-bottom: 1rem;
      }
      .desc {
        font-size: 1rem;
      }
      .site {
        border: 1px solid var(--black100);
        display: block;
        text-align: center;
        padding: 0.625rem 1.5rem;
        margin-top: 1.5rem;
        transition: all 0.2s;

        &:hover {
          background-color: var(--black100);
          color: var(--white);
        }
      }
    }
  }
}
